
import { AmbientLight, BufferGeometry, Float32BufferAttribute, MathUtils, Mesh, MeshStandardMaterial, PerspectiveCamera, Points, PointsMaterial, Scene, SphereGeometry, Texture, TextureLoader, Vector3, WebGLRenderer } from "three";
import Stats from "three/examples/jsm/libs/stats.module";
import { Options, Vue } from 'vue-class-component';

@Options({
    props: {
        numParticles: Number
    }
})
export default class Canvas extends Vue {
    private numParticles!: number;

    private textureLoader!: TextureLoader;

    // Scene
    private scene!: Scene;
    private camera!: PerspectiveCamera;
    private renderer!: WebGLRenderer;

    beforeCreate() {
        this.scene = new Scene();
        this.camera = new PerspectiveCamera( 75, window.innerWidth / window.innerHeight, 0.1, 1000 );
        this.renderer = new WebGLRenderer({
            antialias: true
        });
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        this.renderer.setPixelRatio(window.devicePixelRatio);

        this.textureLoader = new TextureLoader();
        
        window.addEventListener('resize', () => this.resize());
        window.addEventListener('mousemove', (e: MouseEvent) => this.cameramove(e));
        window.addEventListener('touchmove', (e: TouchEvent) => this.cameramove(e.touches[0]));

        this.camera.position.set(0, 0, 100);
        this.camera.lookAt(0, 0, 0);

        [
            new AmbientLight(0xa0a0a0),
        ].forEach(light => this.scene.add(light));
    }

    mounted() {
        document.getElementById("canvas-holder")?.appendChild(this.renderer.domElement);

        this.animate();
        
        this.generateParticles().then(points => this.scene.add(points));

        this.textureLoader.load("../assets/venusmeb.jpg", (texture: Texture) => {
            const material = new MeshStandardMaterial({ map: texture });
            const geom = new SphereGeometry(MathUtils.randFloat(10, 15), 32, 32);

            const planet = new Mesh(geom, material);
            this.scene.add(planet);
        });
    }

    private resize() {
        this.camera.aspect = window.innerWidth / window.innerHeight;
        this.camera.updateProjectionMatrix();
        this.renderer.setSize(window.innerWidth, window.innerHeight);
    }

    private cameramove(e: MouseEvent | Touch) {
        const mousePos = new Vector3(
            (e.clientX / window.innerWidth) * 20 - 1,
            (e.clientY / window.innerHeight) * 20 + 1,
            0
        );

        this.camera.lookAt(mousePos);
    }

    private animate() {
        requestAnimationFrame(this.animate.bind(this));
        this.renderer.render(this.scene, this.camera);
    }

    private generateParticles(): Promise<Points> {
        return new Promise(resolve => {
            const vertices: number[] = [];
            const geom = new BufferGeometry();
            
            for (let i = 0; i < this.numParticles; i++) {
                vertices.push(MathUtils.randFloatSpread(2000));
                vertices.push(MathUtils.randFloatSpread(2000));
                vertices.push(MathUtils.randFloatSpread(2000));
            }

            geom.setAttribute('position', new Float32BufferAttribute(vertices, 3));

            resolve(new Points(geom, new PointsMaterial({ color: 0x888888 })));
        });
    }

}
